@import '../../sassisms.scss';;

.medias-root {
  margin-top: $below-header;
  margin-bottom: $mobile-gutter;
  margin-left: $mobile-gutter;
  margin-right: $mobile-gutter;
}

.medias-header {
  text-align: center;
}

.medias-results {
  justify-content: center;
}

.detail-search {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  left: 0;
  z-index: 2;
  height: 40px;
  border: solid 1px black;

  input {
    height: 100%;
    width: 100%;
    font-size: 24px;
  }
}

.filter-section {
  margin-top: $mobile-gutter;
  margin-bottom: $mobile-gutter;
  width: 100%;
  position: fixed;
  top: 60px;
  height: 100vh;
  left: 0;
  background-color: $modal-background;

  .filter-option {
    margin-left: $mobile-gutter;
    margin-right: $mobile-gutter;
    margin-top: 20px;

    select {
      width: 100%;
    }
  }
}

.hide-filters {
  display: none;
}

.clear-button {
  height: 40px;

  input {
    height: 100%;
    width: 100%;
  }
}



