@import '../../sassisms.scss';;

.media-card {
  cursor: pointer;
  display: flex;
  min-height: 240px;
  background-color: $secondary-background;
  text-decoration: none;
  margin-bottom: 10px;
  margin-top: 10px;
  background-repeat:no-repeat;
  background-size:160px auto;
  background-position: left center;
}

.media-card:hover {
  background-color: $primary-foreground;
  color: $primary-background;
}

.media-card-content {
  margin-left: 170px;
  margin-right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;

  div {
    padding: 5px;
    width: 100%;
    font-size: 0.75em;
  }
}

.media-card-image {
  max-height: 240px;
  width: 160px;
  min-width: 160px;
}


