@import '../../sassisms.scss';;

$mobile-gutter: 40px;

.media-details-root {
  margin-top: $below-header;
  margin-bottom: $mobile-gutter;
  margin-left: $mobile-gutter;
  margin-right: $mobile-gutter;

  a {
    text-decoration: none;
  
    &:link, &:visited, &:hover, &:active {
      border-bottom: 1px solid #67c9ff;
    }
  }
}

.media-details-root h4 {
  margin-top: 0px;
  margin-bottom: 5px;
}

.media-details-title h3 {
  margin-left: 15px;
}

.media-information {
  justify-content: center;
  display: flex;
  flex-wrap: wrap;
}

.media-information div {
  justify-content: center;
}

.media-poster {
  display: flex;
  width: 100%;
  justify-content: center;
}

.media-poster img {
  width: 100%;
  min-width: 200px;
  max-width: 200px;
  height: 100%;
  margin-bottom: 15px;
}

.media-review-img{
  width: 60%;
  display: block;
  margin: auto;
  margin-top: 20px;
}

ul {
  margin-bottom: 10px !important;
}

ul ul {
  margin-bottom: 0px !important;
}

li {
  text-indent: -1em; 
  padding-left: 1em;
}

.right-information {
  width: 100%;
}

.left-information {
  width: 100%;
}


